import { MeshDistortMaterial, Mask } from '@react-three/drei'
import { useFrame, useThree } from '@react-three/fiber';
import { useRef } from 'react'
import { Frame } from "./Frame"

export function CustomMask() {
    const hoverMask = useRef()

    const { viewport } = useThree()
    // viewport = canvas in 3d units (meters)

    const cursor = {}
    cursor.x = 0
    cursor.y = 0
    const parallax = {}
    parallax.x = 0
    parallax.y = 0
    /* window.addEventListener('mousemove', (event) => {
        cursor.x = event.clientX / window.innerWidth - .5
        cursor.y = event.clientY / window.innerHeight - .5
    }) */
    
    useFrame(({ mouse }) => {
        /* parallax.x = cursor.x
        parallax.y = - cursor.y
        hoverMask.current.position.x += (parallax.x - hoverMask.current.position.x) * .1
        hoverMask.current.position.y += (parallax.y - hoverMask.current.position.y) * .1

        console.log(cursor.x) */

        const x = (mouse.x * viewport.width) * .029
        const y = (mouse.y * viewport.height) * .029
        hoverMask.current.position.set(x, y, -1)

        console.log(x)
    })
    

    return <>
        <group>
            <group ref={ hoverMask } position-z={ -1 } scale={ .07}>
                {/* <Frame/> */}
                <Mask id={1}>
                    {(spread) => (
                        <>
                            <circleGeometry args={[0.8, 64]} />
                            <MeshDistortMaterial distort={0.5} radius={1} speed={4} {...spread}/>
                        </>
                    )}
                </Mask>
            </group>
        </group>
    </>
}