import { useRef } from 'react'
import { Html, PivotControls, useMask } from '@react-three/drei'

export function CustomSphere() {
    const sphere = useRef()
    const stencil = useMask(1, false)

    return <>
        {/* <PivotControls anchor={ [ 0, 0, 0 ] } depthTest={ false }> */}
            <mesh position-x={ 0 } ref={ sphere }>
                <sphereGeometry />
                <meshStandardMaterial color="orange" {...stencil} />
                <Html
                    position={ [ 1, 1, 0 ] }
                    wrapperClass="label"
                    center
                    distanceFactor={ 8 }
                    occlude={ [ sphere ] }
                >Prueba</Html>
            </mesh>
        {/* </PivotControls> */}
    </>
}